<template>
  <gs-modal
    v-model="visible"
    :show-confirm="false"
    title="API详情"
    width="90%"
    top="60px"
    cancel-text="关闭"
    @visible-change="visibleChange"
    @cancel="$emit('close')"
  >
    <div class="api-detail-page-modal">
      <div class="api-detail-body">
        <gs-loading :is-show="isDetailLoading" />
        <div class="api-base-info">
          <div class="info-text">
            <div class="api-name">
              {{ detailData.name || '-' }}
              <span
                :class="{'status-label': true, 'status-published': detailData.publish}"
              >{{ detailData.publish ? '已发布' : '未发布' }}</span>
            </div>
            <div class="info-item">
              <div class="info-item-text">
                创建人：{{ detailData.keeper || '-' }}
              </div>
              <div class="info-item-text">
                创建时间：{{ detailData.generateTime || '-' }}
              </div>
              <div class="info-item-text">
                修改人：{{ detailData.updater || '-' }}
              </div>
              <div class="info-item-text">
                修改时间：{{ detailData.updateTime || '-' }}
              </div>
            </div>
          </div>
        </div>
        <div class="api-extract-info">
          <gs-tabs
            v-model="activeTab"
            type="content-tab"
          >
            <gs-tab-pane
              label="API信息"
              name="info"
            >
              <div class="info-body">
                <div class="info-body-base">
                  <div class="info-body-base-title">
                    基本信息
                  </div>
                  <div class="info-body-base-item">
                    <div class="info-item-left">
                      <div class="info-item-left-text">
                        <span class="info-label">API名称：</span>{{ detailData.name || '-' }}
                      </div>
                      <div class="info-item-left-text">
                        <span class="info-label">API分组：</span>{{ detailData.groupName || '-' }}
                      </div>
                      <div class="info-item-left-text">
                        <span class="info-label">版本：</span>{{ detailData.version || '-' }}
                      </div>
                      <div class="info-item-left-text">
                        <span class="info-label">描述：</span>{{ detailData.description || '-' }}
                      </div>
                    </div>
                    <div class="info-item-right">
                      <div class="info-item-right-text">
                        <span class="info-label">协议类型：</span>{{ detailData.protocol || '-' }}
                      </div>
                      <div class="info-item-right-text">
                        <span class="info-label">请求方式：</span>{{ detailData.requestMode || '-' }}
                      </div>
                      <div class="info-item-right-text">
                        <span class="info-label">返回类型：</span>{{ detailData.returnType || '-' }}
                      </div>
                      <div class="info-item-right-text">
                        <span class="info-label">API路径：</span>{{ detailData.path || '-' }}
                      </div>
                    </div>
                  </div>
                </div>
                <div class="info-body-data">
                  <div class="info-body-data-title">
                    数据信息
                  </div>
                  <div class="info-body-data-item">
                    <div class="info-item-text">
                      数据库：{{ detailData.dbName || '-' }}
                    </div>
                    <div class="info-item-text">
                      数据表：{{ detailData.tableName ? detailData.tableDisplayName + '（' + detailData.tableName + '）' : '-' }}
                    </div>
                    <div class="info-item-text">
                      数据Owner：{{ dataOwners }}
                    </div>
                  </div>
                  <div class="info-body-data-table">
                    <el-table
                      :data="apiTableData"
                    >
                      <el-table-column
                        type="index"
                        label="序号"
                      />
                      <el-table-column
                        prop="columnName"
                        label="名称"
                        show-overflow-tooltip
                      >
                        <template slot-scope="scope">
                          <span>{{ scope.row.displayName + '（' + scope.row.columnName + '）' }}</span>
                        </template>
                      </el-table-column>
                      <el-table-column
                        prop="filter"
                        label="筛选字段"
                      >
                        <template slot-scope="scope">
                          {{ scope.row.filter ? '是' : '否' }}
                        </template>
                      </el-table-column>
                      <el-table-column
                        prop="fill"
                        label="必传"
                      >
                        <template slot-scope="scope">
                          {{ scope.row.fill ? '是' : '否' }}
                        </template>
                      </el-table-column>
                      <el-table-column
                        prop="defaultValue"
                        label="缺省值"
                      />
                    </el-table>
                  </div>
                </div>
              </div>
            </gs-tab-pane>
            <gs-tab-pane
              label="测试"
              name="test"
            >
              <div class="info-body">
                <div class="req-msg">
                  <h5 class="title">
                    请求信息
                  </h5>
                  <div class="request">
                    <div
                      v-for="(item, index) in testData"
                      :key="index"
                      class="fields"
                    >
                      <gs-checkbox
                        v-model="item.check"
                        class="name"
                      >
                        <span
                          :title="item.displayName + '（' + item.columnName + '）'"
                        >{{ item.displayName + '（' + item.columnName + '）' }}</span>
                      </gs-checkbox>
                      <span class="value">
                        <fields-input
                          v-model="item.value"
                          :type="item.type"
                        />
                      </span>
                    </div>
                    <div class="fields">
                      <span class="name">limit</span>
                      <span class="value">
                        <gs-input-number
                          v-model="limit"
                          :precision="0"
                        />
                      </span>
                    </div>
                    <div class="fields">
                      <span class="name">offset</span>
                      <span class="value">
                        <gs-input-number
                          v-model="offset"
                          :precision="0"
                        />
                      </span>
                    </div>
                    <div class="gap" />
                  </div>
                  <div class="test-operation">
                    <gs-button
                      :disabled="!btnLoading"
                      @click="stopTest"
                    >
                      停止测试
                    </gs-button>
                    <gs-button
                      :loading="btnLoading"
                      type="primary"
                      @click="startTest"
                    >
                      开始测试
                    </gs-button>
                  </div>
                </div>
                <div class="rep-res">
                  <h5 class="title">
                    返回数据
                  </h5>
                  <div class="response">
                    <gs-tabs
                      v-model="activeName"
                      type="content-tab"
                    >
                      <gs-tab-pane
                        label="编码模式"
                        name="json"
                      >
                        <gs-textarea
                          :value="testResStringify"
                          :autosize="{
                            minRows: 8,
                            maxRows: 12
                          }"
                          readonly
                        />
                      </gs-tab-pane>
                      <gs-tab-pane
                        label="表格模式"
                        name="table"
                      >
                        <el-table
                          :data="testRes"
                        >
                          <el-table-column
                            v-for="(item, index) in tableHeader"
                            :key="index"
                            :label="item"
                            :prop="item"
                          >
                            <template slot-scope="scope">
                              {{ scope.row[item] + '' }}
                            </template>
                          </el-table-column>
                        </el-table>
                      </gs-tab-pane>
                    </gs-tabs>
                  </div>
                </div>
              </div>
            </gs-tab-pane>
          </gs-tabs>
        </div>
      </div>
    </div>
  </gs-modal>
</template>

<script>
import * as service from 'src/service/business-obj/business';
import './style.scss';
import fieldsInput from 'src/components/fields-input';
import mixins from './api-test-mixins';

export default {
  components: {
    fieldsInput
  },
  mixins: [mixins],
  props: {
    id: {
      type: [String, Number],
      default: ''
    },
    show: {
      type: Boolean,
      default: false
    },
    module: {
      type: String,
      default: 'business-obj'
    }
  },
  data() {
    return {
      apiDetail: {},
      apiId: '',
      activeTab: 'info',
      isDetailLoading: false,
      visible: false
    };
  },
  computed: {
    detailData() {
      return this.apiDetail || {};
    },
    apiTableData() {
      return this.detailData.fields || [];
    },
    dataOwners() {
      const owners = (this.detailData.dataOwner || []).map(i => i.name) || [];
      return owners.join('，') || '-';
    }
  },
  watch: {
    show(value) {
      this.visible = value;
    }
  },

  methods: {
    visibleChange(val) {
      if (val && this.id) {
        this.isDetailLoading = true;
        service.getApiDetailInfo(this.id, this.module === 'business-management').then(data => {
          this.apiDetail = data;
          this.isDetailLoading = false;
          this.testData = (data.fields || [])
            .filter(item => item.filter)
            .map(item => {
              return {
                columnName: item.columnName,
                displayName: item.displayName,
                value: '',
                check: true,
                type: item.type,
                fill: item.fill
              };
            });
        });
      }
    }
  }
};
</script>
